@import "https://fonts.googleapis.com/css2?family=Rubik&display=swap";
@import "https://fonts.googleapis.com/css2?family=Italiana&display=swap";

*, :before, :after {
  box-sizing: border-box;
}

ul[class], ol[class] {
  padding: 0;
}

body, h1, h2, h3, h4, p, ul[class], ol[class], li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
}

ul, ol {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img {
  max-width: 100%;
}

article > * + * {
  margin-top: 1em;
}

input, button, textarea, select {
  font: inherit;
}

html {
  scroll-behavior: smooth;
}

body {
  overflow-anchor: none;
  font-family: Rubik, sans-serif;
}

.container {
  max-width: 1500px;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  min-height: 100px;
  width: 100%;
  background: linear-gradient(89.5deg, #d3c8f08a 5.04%, #f7dee28a 50.79%, #edd8e68a 96.53%);
}

.header__top {
  justify-content: flex-start;
  align-items: center;
  padding: 20px;
  display: flex;
  position: relative;
}

@media screen and (width <= 840px) {
  .header__top {
    padding: 20px 0;
  }
}

@media screen and (width <= 700px) {
  .header__top {
    padding: 0;
  }
}

.header__logo a.header__logo-title {
  color: #212121;
  align-items: center;
  font-family: Italiana, serif;
  font-size: 56px;
  font-style: normal;
  font-weight: 400;
  line-height: 66px;
  text-decoration: none;
  display: flex;
}

@media screen and (width <= 840px) {
  .header__logo a.header__logo-title {
    font-size: 40px;
  }
}

@media screen and (width <= 700px) {
