    font-size: 35px;
  }
}

.header__logo a.header__logo-title:hover {
  color: #8570bd;
  transition: all .3s ease-in-out;
}

.header__search {
  flex-grow: 1;
  margin: 0 19px;
  position: relative;
}

@media screen and (width <= 700px) {
  .header__search {
    display: none;
  }
}

.header__search input {
  width: 100%;
  height: 55px;
  border: none;
  border-radius: 35px;
  padding: 17px 50px;
  position: relative;
}

@media screen and (width <= 700px) {
