  height: 15px;
  background-color: #212121c2;
}

@media screen and (width <= 530px) {
  .slider-body .slick-dots li.slick-active button {
    width: 11px;
    height: 11px;
  }
}

.slider-body .slick-dots button {
  width: 12px;
  height: 12px;
  background-color: #21212180;
  border: none;
  border-radius: 50%;
