.modal_overlay {
  height: 100vh;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
  background-color: #00000037;
  justify-content: center;
  align-items: center;
  transition: all .3s ease-in-out;
  display: flex;
  position: fixed;
  inset: 0;
}

.modal_card {
  width: 80%;
  height: 80vh;
  background-color: #fff;
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: none;
}

.modal_card button {
  width: 60%;
  height: 50px;
  color: #fff;
  background-color: #8570bd;
  border: none;
  border-radius: 10px;
  margin: 0 auto;
  transition: all .3s;
  display: block;
}

.modal_card button:hover {
  background-color: #7260a5ad;
  transition: all .5s;
  scale: 1.04;
}

.modal_close {
  width: 50px;
  height: 50px;
  background-color: #fff;
  border-radius: 25px;
  position: absolute;
  top: 0;
  right: 0;
}

.modal_close:hover {
  background-color: #fff7;
}

.modal_close-wrapper {
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.modal_close-wrapper span:first-child {
  width: 35px;
  height: 3px;
  background-color: #4d4b4b;
  border-radius: 10px;
  transition: all .3s ease-in-out;
  position: absolute;
  transform: rotate(45deg);
}

.modal_close-wrapper span:nth-child(2) {
  width: 35px;
  height: 3px;
  background-color: #4d4b4b;
  border-radius: 10px;
  transition: all .3s ease-in-out;
  position: absolute;
  transform: rotate(-45deg);
}

.modal_close-wrapper:hover span:first-child, .modal_close-wrapper:hover span:nth-child(2) {
  background-color: #ffffffde;
  transition: all .3s ease-in-out;
}

.modal_card-name {
  text-align: center;
  margin-top: 30px;
  font-size: 36px;
}

@media screen and (width <= 450px) {
  .modal_card-name {
    margin-top: 10px;
    font-size: 34px;
  }
}

@media screen and (width <= 380px) {
  .modal_card-name {
    margin-top: 0;
  }
}

.modal_info {
  padding: 20px;
  display: flex;
}

@media screen and (width <= 600px) {
  .modal_info {
    flex-direction: column;
  }
}

.modal_img {
  width: 50%;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  display: flex;
}

@media screen and (width <= 1000px) {
  .modal_img {
    width: 80%;
  }
}

@media screen and (width <= 600px) {
  .modal_img {
    width: 100%;
  }
}

.modal_img img {
  max-width: 400px;
  max-height: 600px;
