.cart-modal {
  width: 100vw;
  height: 100%;
  z-index: 100000;
  visibility: hidden;
  opacity: 0;
  background-color: #00000037;
  justify-content: center;
  align-items: center;
  margin: auto;
  transition: all .5s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.cart-modal.open {
  opacity: 1;
  visibility: visible;
}

.cart-modal__box {
  max-width: 800px;
  min-width: 400px;
  z-index: 1;
  background-color: #fff;
  border-radius: 25px;
  margin: auto;
  padding: 45px;
  position: relative;
}

