.pageSwap {
  justify-content: center;
  margin: 20px 15px;
  display: flex;
}

.pageSwapprevious {
  width: 50px;
  height: 50px;
  cursor: pointer;
  background-color: #8570bd;
  border: none;
  border-radius: 100%;
  margin-right: 5px;
  padding: 5px;
  transition: all .2s;
}

.pageSwapprevious:hover {
  background-color: #f7f7f7d8;
  transition: all .5s;
}

.pageSwapprevious:hover span {
  border: 5px solid #8570bd;
  border-width: 0 5px 5px 0;
  transition: all .5s;
}

.pageSwapprevious span {
  width: 30px;
  height: 30px;
  border: 5px solid #fff;
  border-width: 0 5px 5px 0;
  border-radius: 3px;
  margin-left: 8px;
  transition: all .2s;
  display: block;
  transform: rotate(135deg);
}

.pageSwapnext {
  width: 50px;
  height: 50px;
  cursor: pointer;
  background-color: #8570bd;
  border: none;
  border-radius: 100%;
  margin-left: 5px;
  transition: all .2s;
  display: block;
}

.pageSwapnext:hover {
  background-color: #f7f7f7d8;
  transition: all .5s;
}

.pageSwapnext:hover span {
  border: 5px solid #8570bd;
  border-width: 0 5px 5px 0;
  transition: all .5s;
}

.pageSwapnext span {
  width: 30px;
  height: 30px;
  border: 5px solid #fff;
  border-width: 0 5px 5px 0;
  border-radius: 3px;
  transition: all .2s;
  display: block;
  transform: rotate(-45deg);
}

.pageSwapnext--hide {
  background-color: #8570bd5d;
}

.pageNumber {
